@import url(https://fonts.googleapis.com/css?family=Roboto|Roboto+Slab:900&display=swap&subset=latin-ext);
html{box-sizing:border-box}*,*::before,*::after{box-sizing:inherit}html,body{min-height:100%}body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol'}code,kbd,samp,pre{font-family:SFMono-Regular, Consolas, 'Liberation Mono', Menlo, Courier, monospace;font-size:1em}body{-webkit-tap-highlight-color:transparent;-webkit-touch-callout:none}button{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}h1{font-size:2rem}h2{font-size:1.5rem}h3{font-size:1.17rem}h4{font-size:1rem}h5{font-size:0.83rem}h6{font-size:0.67rem}:root{--bg-gradient: linear-gradient(180deg, #f1ca7f 0%, #fcb098 100%);--body-text-color: #280707;--header-text-color: rgba(255,231,231,0.83);--accent-color: #d31212;--accent-focus-color: rgba(211,18,18,0.55)}body{font-family:"Roboto",sans-serif;font-weight:400;font-size:16px;line-height:1.5;margin:0}

